/****************************/
/* Spacedog homepage styles */
/****************************/

body.index{

header {
  .logo { font-family: @font-family-logo; }
}

section { 
  margin-top: 100px;
  h2 {
    font-family: @font-family-header;
    font-size: @font-size-x-large;
  }
  color: white;
}

section#splash {
  margin:0;

  .overlay { 
    position: relative;
  }

  #console {
    background-color: transparent;
    background-image: url('/static/splash/console.png');
    z-index: 9001;
  }
  #stars {
    background-image: url('/static/splash/stars.png');
    background-attachment: fixed;
  }

  #rollover {
    
  }

  .overlay.text {
    color:@white;
    h1 {
      margin: 15% auto 15px auto;
      padding-left: 11%;
      font-size: 4em;
    }
    .tagline {
      font-family: @font-family-header;
      font-size: @font-size-large;
      margin-left: 11%;
      width: 65%;
    }
  }
}

section:not(#splash).fullwidth {
  h2 {
    margin: 12.5% 7.5% 0 7.5%;
  }
  h3 {
    margin: 10px 7.5%;
  }
  .wrapper {
    margin: 0 6.5%;
  }
}

section#how-we-work {
  margin-top: 150px;

  background-color: #783F17;
  background-image: url('/static/splash/display-orange.png');

  &> ul {
    .make-row();
    margin: 50px 0 0 0;

    &> li {
      .make-column(4);

      div {
        padding: 20px;
        background: #F7B182;
        .box-shadow(5px 5px #B55E24);
        color: @black;
        margin-bottom: 20px;
      }

      h3 {
        margin: 0;
        font-size: @font-size-large;
        text-align: center;
      }

      ul {
        margin: 20px 0 0 0;
        padding:10px 20px;

        li {
          list-style-type: circle;
          padding: 0;
        }
      }
    }
  }
}

section#testimonials {
  background-color: #0A3E52;
  background-image: url('/static/splash/display-blue.png');

  &> ul {
    .make-row();

    &> li {
      .make-column(6);

      &.left {
        clear: left;
      }
      div {
        padding: 20px;
        color: @black;
        background: #72B1C8;
       .box-shadow(5px 5px #0E5D7A);
        margin-bottom: 20px;
      }


      span.quote {
        font-size: @font-size-large;
        font-style: bold;
        &:before {
          content: "\201C";
          margin-right: 2px;
        }
        &:after {
          content: "\201D";
          margin-left: 2px;
        }
      }
      span.more {
        display: block;
        font-size: @font-size-small;
      }
      span.person {
        .text {
          display: block;
          text-align: right;
        }
      }
    }
  }
}

section#accomplishments {
  background-color: #58595B;
  background-image: url('/static/splash/display-gray.png');

  ul {
    li {
      .make-row();

      div {
        .clearfix();        
      }

      margin-bottom: 35px;
    }
    .description { .make-column(8); }
    .stat {
      margin-left: 20px;
      em { font-weight: bold; font-style: normal; }
    }
    .logo {
      .make-column(3);
      .make-column-offset(1);
      img { float: left; }
    }
  }
}

section#team {
  background-color: #58595B;
  background-image: url('/static/splash/display-gray.png');

  ul { 
    width: 80%;
    padding-left: 5%;

    li {
      .clearfix();
      margin-bottom: 10px;

      img {
        float: left;
        margin-right: 2%;
      }
      .about { 
        float: left;

        width: 60%;
        margin: 20px;
      }
      .name { font-weight: bold; }
    }
  }
}

}